import { useEffect } from "react";
import { useState } from "react";
import Axios from "axios";

function useAxios(metodo, url, noEjectuarAlInicio = false, parametros) {
  const [respuestaServer, setRespuestaServer] = useState(null);
  const [datos, setDatos] = useState([]);
  const [error, setError] = useState(null);
  const [cargando, setCargando] = useState(false);
  const resetearConsulta = () => {
    setRespuestaServer(null);
    setDatos([]);
    setError(null);
    setCargando(false);
  };
  const actualizarParametros = (nuevosParametros) => {
    parametros = nuevosParametros;
  };
  const actualizarUrl = (nuevaUrl) => {
    url = nuevaUrl;
  };
  const crearInstanciaAxios = () => {
    const instance = Axios.create({
      // baseURL: "http://192.168.1.159:5000",
      baseURL: "http://190.223.56.109:5000",
      // baseURL: "http://localhost:5000",
    });
    return instance;
  };
  const enviarPeticion = async (instance) => {
    let respuesta = null;
    switch (metodo) {
      case "POST":
        respuesta = await instance.post(url, parametros);
        break;
      case "GET":
        respuesta = await instance.get(url);
        break;
      default:
        break;
    }
    return respuesta;
  };
  const iniciarPeticion = () => {
    resetearConsulta();
    recargarPeticion();
  };

  const recargarPeticion = async () => {
    setCargando(true);
    try {
      const instanciaAxios = crearInstanciaAxios();
      const respuesta = await enviarPeticion(instanciaAxios);
      setRespuestaServer(200);
      respuesta && setDatos(respuesta.data);
      setCargando(false);
    } catch (error) {
      setRespuestaServer(400);
      setDatos([]);
      setCargando(false);
      setError(error);
    }
  };

  useEffect(() => {
    if (!noEjectuarAlInicio) {
      iniciarPeticion();
    }
    return () => {
      Axios.Cancel();
    };
    //eslint-disable-next-line
  }, [noEjectuarAlInicio]);
  return {
    datos,
    cargando,
    error,
    setDatos,
    actualizarParametros,
    iniciarPeticion,
    recargarPeticion,
    actualizarUrl,
    resetearConsulta,
    respuestaServer,
  };
}

export default useAxios;
