import React from "react";
import { Button } from "../../atoms/Button";
import { NoticeStyled } from "./styled";

const Notice = ({data}) => {
  return (
    <NoticeStyled>
      <div class="type-post">
        <div class="entry-cover">
          <a title="Blog" href="#">
            <img alt="blog" src={data.imageSource} />
          </a>
          <div class="post-date-bg">
            <div class="post-date">
              {data.day}
              <span>{data.month === "Noviembre" ? "NOV" : data.month}</span>
            </div>
          </div>
        </div>
        <div class="latest-news-content">
          <div class="entry-header">
            <h3 class="entry-title">
              <a title="Patients Share Success Stories" href="#">
                {data.title}
              </a>
            </h3>
          </div>
          <div class="entry-content">
            <p>
              {data.sortDescription}
            </p>
          </div>
          <Button primary>
            Leer más
          </Button>
        </div>
      </div>
    </NoticeStyled>
  );
};

export default Notice;
