import styled from 'styled-components';
import { Flex } from '../../../Theme/FlexStyled';

export const SectionMenuStyled = styled(Flex)`
    width: 90%;
    margin: 0 auto 2em auto;
    gap: 20px;
    flex-wrap: wrap;
    /* border-top: solid 40px ${({theme}) => theme.colors.primary}; */
    padding: 2em 0;
    border-bottom: solid 1px ${({theme}) => theme.colors.primary};
`;

export const SectionMenuHeader = styled.h5`
    width: 90%;
    margin: 2em auto 0 auto;
    border-top: solid 1px ${({theme}) => theme.colors.primary};
    border-radius: 40px 40px 0 0;
    background-color: ${({theme}) => theme.colors.primary};
    text-align: center;
    color: #fff;
    font-family: 'Raleway', sans-serif;
    font-size: 23px;
    letter-spacing: 4.6px;
    padding-top: ${props => props.addContent ? "20px": "10px"};
    padding-bottom: ${props => props.addContent ? "20px": "10px"};;
    text-transform: uppercase;
`