import React, { useEffect, useState } from "react";
import useAxios from "../../../hooks/useAxios";
import { Row } from "../../../Theme/FlexStyled";
import { Button } from "../../atoms/Button";
import Input from "../../atoms/Input";
import SERVICE from "../../../images/services.jpg";
import {
  ConsultaTuCitaStyled,
  FormStyled,
  Bloque,
  Message,
  CarouselStyled,
} from "./styled";
import Carousel from "react-multi-carousel";
import { responsive } from "./responsive";
import Ticket from "../../molecules/Ticket";
function ConsultaTuCita() {
  const [message, setMessage] = useState(null);
  const ERROR_MESSAGE =
    "Ocurrió un error en la consulta, por favor intentarlo nuevamente.";
  const NO_CONSULTAS_MESSAGE = "Usted no tiene consultas pendientes.";
  const consultas = useAxios("GET", "", true);
  const [dni, setDni] = useState("");
  const [nombre, setNombre] = useState("FRANK NOLASCO VELASQUEZ");
  const [consultasVisibles, setConsultasVisibles] = useState(true);

  const resetearBloque = () => {
    setNombre("");
    setConsultasVisibles(false);
  };

  useEffect(() => {
    consultas.datos.length !== 0 && setNombre(consultas.datos[0].PACIENTE);
    comprobarRespuesta();
    return () => {};
    //eslint-disable-next-line
  }, [consultas.datos]);

  const comprobarRespuesta = () => {
    if (consultas.respuestaServer === 200) {
      if (consultas.datos.length !== 0) {
        setConsultasVisibles(true);
      } else {
        setMessage({
          severity: "warn",
          summary: "PACIENTE SIN CONSULTAS : ",
          detail: NO_CONSULTAS_MESSAGE,
        });
      }
    }
    if (consultas.respuestaServer === 400) {
      resetearBloque();
      setMessage({
        severity: "error",
        summary: "ERROR DE SEVIDOR",
        detail: ERROR_MESSAGE,
      });
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    resetearBloque();
    consultas.actualizarUrl(`/api/consultas/${dni}`);
    consultas.iniciarPeticion();
  };

  return (
    <ConsultaTuCitaStyled>
      <div>
        <FormStyled onSubmit={handleSubmit}>
          <Row justifyContent="center" gap="20px">
            <Input
              placeholder="Ingrese su dni"
              value={dni}
              onChange={(e) => setDni(e.target.value)}
            />
            <Button primary>
              <i className="fa fa-heart" />
              <span>Consultar</span>
            </Button>
          </Row>
        </FormStyled>
        {message && (
          <Message severity={message.severity}>{message.detail}</Message>
        )}
        <Bloque activo={consultasVisibles}>
          <p className="bienvenida">
            ¡Hola, Bienvenido(a) <strong>{nombre}</strong>!
          </p>
          <div>
            <div className="card-tickes">
              <h2 className="card-title-tickes">
                <strong>Tiene las siguientes citas:</strong>
              </h2>

              {consultas.datos.map((c) => (
                <span>TIENE</span>
              ))}
            </div>
          </div>
        </Bloque>
        {consultasVisibles && (
          <CarouselStyled infinite responsive={responsive}>
            <Ticket />
            <Ticket />
            <Ticket />
            <Ticket />
            <Ticket />
          </CarouselStyled>
        )}
      </div>
      <img src={SERVICE} alt="citas web service" />
    </ConsultaTuCitaStyled>
  );
}

export default ConsultaTuCita;
