import React from 'react'
import { WidgetCardImage } from './styled'

const CardImage = ({src, title}) => {
    return (
        <WidgetCardImage>
            <div class="testimonial-box">
            <div class="testimonial-content">
              <i><img src={src} alt="testimonial"/></i>
              <h5>{title}</h5>
            </div>
          </div>
        </WidgetCardImage>
    )
}

export default CardImage
