import React from "react";
import Carousel from "react-multi-carousel";
import styled from "styled-components";
import { responsive } from "./responsive";
const ImageS = styled.div`
  width: 100%;
  position: relative;
  ::before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    background-image: url(${(props) => props.src});
    background-size: contain;
    filter: blur(20px);
    height: 100%;
  }

  img {
    width: 100%;
    height: calc(100vh - 120px);
    object-fit: contain;
    z-index: 100;
  }
`;

const CarouselStyled  = styled(Carousel)`
    .react-multi-carousel-item {
        position: relative;
    }
    
`
const Image = (props) => (
  <ImageS src={props.src}>
    <img {...props} />
  </ImageS>
);

const PrimaryCarousel = () => {
  return (
    <div>
      <CarouselStyled responsive={responsive} infinite>
        <Image src="https://www.redaccionmedica.com/images/destacados/un-hospital-bien-disenado-mejora-la-salud-de-los-pacientes-7674.jpg" />
        <Image src="https://www.redaccionmedica.com/images/destacados/un-hospital-bien-disenado-mejora-la-salud-de-los-pacientes-7674.jpg" />
        <Image src="https://www.redaccionmedica.com/images/destacados/un-hospital-bien-disenado-mejora-la-salud-de-los-pacientes-7674.jpg" />
        <Image src="https://www.redaccionmedica.com/images/destacados/un-hospital-bien-disenado-mejora-la-salud-de-los-pacientes-7674.jpg" />
        <Image src="https://www.redaccionmedica.com/images/destacados/un-hospital-bien-disenado-mejora-la-salud-de-los-pacientes-7674.jpg" />
      </CarouselStyled>
    </div>
  );
};

export default PrimaryCarousel;

