import React from 'react'
import SectionHeader from '../../atoms/SectionHeader'
import { SectionStyled } from './styled'

const Section = ({children, header=null, background="#FFF", color="#333", ...rest}) => {
    return (
        <SectionStyled {...{background, color, ...rest}}>
            {header && <SectionHeader {...{background, color}}> {header}</SectionHeader> }
            {children}
        </SectionStyled>
    )
}

export default Section
