import React from 'react'
import { SectionMenuStyled, SectionMenuHeader } from './styled'

const SectionMenu = ({children, header=null, ...rest}) => {
    return (
        <div {...rest}>
            <SectionMenuHeader addContent={!header}>
                {header}
            </SectionMenuHeader>
            <SectionMenuStyled>
                {children}
            </SectionMenuStyled>
        </div>
    )
}

export default SectionMenu
