import React from 'react'
import Carousel from 'react-multi-carousel'
import TEAM1 from '../../../images/medicos/team-2.png'
import TEAM2 from '../../../images/medicos/team-3.png'
import TEAM3 from '../../../images/medicos/team-4.png'
import PersonCard from '../../molecules/PersonCard'
import { responsive } from './responsive'
const MedicsCarousel = () => {
    return (
        <Carousel responsive={responsive} infinite style={{margin: "5em 0"}}>
            <PersonCard image={TEAM1} name="Teed Grant" label="PEDIATRA"/>
            <PersonCard image={TEAM2} name="Grant Gustin" label="ELECTROSHOKER"/>
            <PersonCard image={TEAM3} name="Ezra Miller" label="NEUROLOGO"/>
            <PersonCard image={TEAM1} name="Teed Grant" label="PEDIATRA"/>
            <PersonCard image={TEAM2} name="Grant Gustin" label="ELECTROSHOKER"/>
            <PersonCard image={TEAM3} name="Ezra Miller" label="NEUROLOGO"/>
            <PersonCard image={TEAM1} name="Teed Grant" label="PEDIATRA"/>
            <PersonCard image={TEAM2} name="Grant Gustin" label="ELECTROSHOKER"/>
            <PersonCard image={TEAM3} name="Ezra Miller" label="NEUROLOGO"/>
        </Carousel>
    )
}

export default MedicsCarousel
