import styled from "styled-components";

export const TicketStyled = styled.div`
  border: solid 1px ${(props) => props.theme.colors.primary}33;
  margin: auto;
  padding: 0;
  width: 75%;
  align-self: center;
  border-radius: 10px;
  border-radius: 0.5em;
  overflow: hidden;

  .colunm-consult > i {
      width: 35px;
    color: ${(props) => props.theme.colors.primary};
  }
  button > i {
    margin-right: 0;
  }

  .ticket-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.25em 1em;
    background-color: ${(props) => props.theme.colors.primary};
    color: white;
  }

  .ticket-header,
  .ticket-header > .imprimir {
    font-size: 14pt;
  }
  .ticket-body {
    padding: 0 2em;
  }

  .ticket-body > .colunm-consult {
    font-size: 13pt;
  }

  @media screen and (max-width: 900px) {
    .ticket-header,
    .ticket-header > .imprimir {
      font-size: 12pt;
    }

    .ticket-body > .colunm-consult {
      font-size: 10pt;
    }
  }
`;
