import React from "react";
import { Button } from "../../atoms/Button";
import { TicketStyled } from "./styled";

const defaultValues = {
  ESPECIALIDAD: "NEFROLOGÍA",
  FECHA: "20-02-2020",
  TURNO: "TURNO MAÑANA - 10:00 - 12:00",
  MEDICO: "ROBERTO RIGOBERTO NOLE CAMPOS",
  FUENTEFINANCIAMIENTO: "PARTICULAR",
  ORDENPAGO: "3190",
  CUENTA: "104512",
};

const Ticket = ({ data = defaultValues }) => {
  const onClick = (ncuenta) => {
    // TicketExport(ncuenta);
  };
  return (
    <TicketStyled>
      <div className="ticket-header">
        <strong>{data.ESPECIALIDAD}</strong>
        <Button linkWhite onClick={(e) => onClick(data.CUENTA)}>
          <i  className="fa fa-cloud-download-alt"/>
        </Button>
      </div>
      <div className="ticket-body">
        <p className="colunm-consult">
          <i  className="fa fa-calendar-day"/>
          {data.FECHA}
        </p>
        <p className="colunm-consult">
          <i  className="fa fa-bell"/>

          {data.TURNO}
        </p>
        <p className="colunm-consult">
          <i  className="fa fa-user-md"/>
          Dr(a) {data.MEDICO}
        </p>
        <p className="colunm-consult">
          <i  className="fa fa-hand-holding"/>
          {data.FUENTEFINANCIAMIENTO}
        </p>
        {data.FUENTEFINANCIAMIENTO !== "SIS" && (
          <p className="colunm-consult">
            <i  className="fa fa-cash-register"/>
            {data.ORDENPAGO}
          </p>
        )}
      </div>
    </TicketStyled>
  );
};

export default Ticket;
