import React from "react";
import Carousel from "react-multi-carousel";
import styled from "styled-components";
import CardImage from "../../molecules/CardImage";
import { API_LIST_OTHERS_INTEREST_THEMES } from "./API_LIST";
import { responsive } from "./responsive";

const Styled = styled.div`
    margin: 5em 0;
    .react-multi-carousel-item,
    .react-multi-carousel-item--active{
        opacity: .5;
    }
    .react-multi-carousel-item--active:nth-child(1){
        opacity: 1;
    }
`

const OtherInterestTheme = () => {
  return (
    <Styled>
      <Carousel responsive={responsive} swipeable infinite>
        {Array.isArray(API_LIST_OTHERS_INTEREST_THEMES) && API_LIST_OTHERS_INTEREST_THEMES.map((item, index) => <CardImage {...item} />)}
      </Carousel>
    </Styled>
  );
};

export default OtherInterestTheme;
