import * as React from "react";
import styled from "styled-components";
import ImageCardLink from "../components/molecules/ImageCardLink";
import Layout from "../components/scheme/Layout";
import WebMailImage from "../images/menus/web-mail.png";
import LReclImage from "../images/menus/libro-reclamaciones.png";
import EstadisticasImage from "../images/menus/estadisticas.png";
import ConvocatoriasImage from "../images/menus/convocatoria.png";
import BibliotecaImage from "../images/menus/biblioteca.png";
import AtentionHours from "../components/organism/AtentionHours";
import ConsultaTuCita from "../components/organism/ConsultaTuCita";
import Section from "../components/scheme/Section";
import SectionMenu from "../components/scheme/SectionMenu";
import { DefaultTheme } from "../Theme/DefaultTheme";
import OtherInterestTheme from "../components/organism/OtherInterestThemes";
import PrimaryCarousel from "../components/organism/PrimaryCarousel";
import MedicsCarousel from "../components/organism/MedicsCarousel";
import Notices from "../components/organism/Notices";


// markup
const IndexPage = () => {
  return (
    <Layout>
      <PrimaryCarousel />
      <SectionMenu style={{ margin: "10em 0" }}>
        <ImageCardLink Image={WebMailImage}>WEB-MAIL</ImageCardLink>
        <ImageCardLink Image={LReclImage}>LIBRO DE RECLAMACIONES</ImageCardLink>
        <ImageCardLink Image={EstadisticasImage}>ESTADISTICAS</ImageCardLink>
        <ImageCardLink Image={ConvocatoriasImage}>CONVOCATORIAS</ImageCardLink>
        <ImageCardLink Image={BibliotecaImage}>
          BIBLIOTECA VIRTUAL
        </ImageCardLink>
      </SectionMenu>
      <Section header="CONSULTA TUS CITAS WEB">
        <ConsultaTuCita />
      </Section>

      <Section noAll header="ULTIMAS NOTICIAS" background="#dfdfdf" style={{paddingBottom: '1em'}}>
        <Notices />
      </Section>
      <Section header="NUESTROS MEDICOS">
        <MedicsCarousel />
      </Section>

      <SectionMenu header="Servicios">
        <ImageCardLink Image={WebMailImage}>OBSTETRICIA</ImageCardLink>
        <ImageCardLink Image={LReclImage}>ODONTOLOGIA</ImageCardLink>
        <ImageCardLink Image={EstadisticasImage}>OFTALMOLOGIA</ImageCardLink>
        <ImageCardLink Image={ConvocatoriasImage}>OTORRINO</ImageCardLink>
        <ImageCardLink Image={BibliotecaImage}>PEDIATRIA</ImageCardLink>
        <ImageCardLink Image={WebMailImage}>GASTROENTEROLOGIA</ImageCardLink>
        <ImageCardLink Image={LReclImage}>INFECTOLOGÍA</ImageCardLink>
        <ImageCardLink Image={EstadisticasImage}>LABORATORIO</ImageCardLink>
        <ImageCardLink Image={ConvocatoriasImage}>
          CONSULTORIO DE ITS
        </ImageCardLink>
        <ImageCardLink Image={BibliotecaImage}>ENFERMERIA</ImageCardLink>
      </SectionMenu>

      <Section
        header="OTROS TEMAS DE INTERES"
        style={{ paddingBottom: "2em" }}
        noAll
        background={DefaultTheme.colors.primary}
        color="white"
      >
        <OtherInterestTheme />
      </Section>

      <AtentionHours />
    </Layout>
  );
};

export default IndexPage;
