import styled from "styled-components";

export const InputStyled = styled.div`
    input {
        padding: .4em 1em;
        border: none;
        border-bottom: solid 1px #ccc;
        font-family: ${({ theme }) => theme.fonts.fontFamily};
        ::placeholder{
            font-family: ${({ theme }) => theme.fonts.fontFamily};
        }
        :active, :focus {
            outline: 0;
            border-color: ${({ theme }) => theme.colors.primary};
        }
    }

`