import styled from "styled-components";

export const WidgetCardImage = styled.div`
  min-width: 30%;
  /* width: min-content; */
  margin: auto;
  .testimonial-box {
    padding: 0 15px;
    margin-top: 65px;
    opacity: 1;
    outline: none;
  }
  .testimonial-content {
    padding: 20px 35px 30px;
    background-color: ${({ theme }) => theme.colors.primaryCards};
    position: relative;
    text-align: center;
  }
  .testimonial-content > i {
    width: 120px;
    max-width: 120px;
    position: absolute;
    top: -65px;
    left: 0;
    right: 0;
    margin: 0 auto;
  }
  .testimonial-content > i img {
    width: 120px;
    max-width: 120px;
    border: 10px solid rgba(5, 175, 196, 0.5);
    border-radius: 100%;
  }
  .testimonial-content h5 {
    color: #fff;
    border-bottom: 1px solid #fff;
    display: inline-block;
    font-family: "Poppins", sans-serif;
    font-size: 20px;
    font-weight: 700;
    letter-spacing: 0.8px;
    margin-top: 50px;
    margin-bottom: 20px;
  }
  .testimonial-content p {
    color: #fff;
    display: block;
    font-family: "Playfair Display", serif;
    font-size: 20px;
    font-style: italic;
    letter-spacing: 0.8px;
    margin-bottom: 0;
  }
  .testimonial-content::before {
    /*content: "\f10d";*/
    content: "";
    color: #fff;
    font-family: "FontAwesome";
    font-size: 30px;
    position: absolute;
    top: 15px;
    left: 25px;
  }
`;
