import React from 'react'
import { ImageCardLinkStyled } from './styled'

const ImageCardLink = ({ Image, children }) => {
    return (
        <ImageCardLinkStyled>
            <img src={Image} alt="dept" width="80" height="80"/>
            <span>{children}</span>
        </ImageCardLinkStyled>
    )
}

export default ImageCardLink
