import styled from "styled-components";
import BG from "../../../images/footer-bg.jpg";
export const AtentionHoursStyled = styled.div`
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: space-between;
  background-image: url(${BG});
  position: relative;
  z-index: -2;
  ::before {
    content: "";
    background-color: rgba(55, 152, 242, 0.45);
    width: 100%;
    height: 100vh;
    position: absolute;
    z-index: -1;
  }
`;

export const Widget = styled.div`
  min-width: 32%;
  width: min-content;
  .widget-title {
    color: #fff;
    font-size: 20px;
    margin-top: 0;
    margin-bottom: 20px;
    word-wrap: break-word;
    text-transform: capitalize;
  }
  .widget_workinghours {
    background-color: ${({ theme }) => theme.colors.primaryCards};
    border-top-left-radius: 50px;
    border-top-right-radius: 50px;
    padding: 0 35px 35px;
    text-align: center;
  }
  .widget_workinghours > span {
    border-radius: 100%;
    background-color: #fff;
    border: 10px solid ${({ theme }) => theme.colors.primaryCards};
    color: ${({ theme }) => theme.colors.primaryCards};
    display: inline-block;
    font-size: 52px;
    height: 100px;
    width: 100px;
    line-height: 82px;
    margin-top: -50px;
    margin-bottom: 20px;
  }
  .widget_workinghours ul {
    padding-left: 0;
    margin-bottom: 0;
    list-style: none;
  }
  .widget_workinghours ul li {
    border-bottom: 1px solid ${({ theme }) => theme.colors.primary};
    display: inline-block;
    padding: 10px 0 16px;
    width: 100%;
  }
  .widget_workinghours ul li > span {
    color: #fff;
    float: left;
    font-size: 16px;
  }
  .widget_workinghours ul li > b {
    color: #fff;
    float: right;
    font-size: 14px;
  }
  .footer-social {
    display: inline-block;
  }
  .footer-social > i {
    background-color: rgba(0, 0, 0, 0.2);
    border-radius: 100%;
    border: 1px solid #78797b;
    color: #fff;
    display: inline-block;
    height: 42px;
    width: 42px;
    line-height: 42px;
    margin-right: 20px;
    text-align: center;
  }
  .footer-social ul {
    display: inline-block;
    padding-left: 0;
    margin-bottom: 0;
  }
  .footer-social ul li {
    display: inline-block;
    margin-right: 20px;
  }
  .footer-social ul li:last-of-type {
    margin-right: 0;
  }
  .footer-social li a {
    color: #fff;
    -webkit-transition: all 1s ease 0s;
    -moz-transition: all 1s ease 0s;
    -o-transition: all 1s ease 0s;
    transition: all 1s ease 0s;
  }
  .footer-social li a:hover {
    color: var(--primary-color);
  }
`;
