import styled from 'styled-components'

export const SectionStyled = styled.section`
    width: 100%;
    height: ${props => props.noAll ? 'auto': '100vh'};
    background: ${props => props.background};
    color: ${props => props.color};
    scroll-snap-align: start;
    padding: 0 5%;
    padding-top: .25em;
`