import styled from "styled-components";

export const ImageCardLinkStyled = styled.div`
    width: calc(20% - 20px);
    border: solid 1px #f3f3f3;
    border-radius: 0.5em;
    padding: 2em;
    display: flex;
    flex-direction: column;
    transition: all 0.3s ease-in-out;
    justify-content: center;
    align-items: center;
    gap: 20px;
    margin: auto;
    cursor: pointer;
    > span {
        text-align: center;
    }
    :hover{
        transform: scale(1.05);
    }
`