import Carousel from "react-multi-carousel";
import styled from "styled-components";

export const ConsultaTuCitaStyled = styled.div`
    width: 100%;
    border-radius: 50px;
    padding: 1em;
    display: flex;
    justify-content: space-around;
    input, button {
        font-size: 20px;
    }

    > div {
        width: 70%;
        /* border: solid 1px #000; */
        padding: 1em 0;
    }

    
    img {
        width: 320px;
        align-self: flex-end;
    }

    @media screen and (max-width: 900px){
        > div {
            width: 100%;
        }
        img {
            display: none;
        }
    }
    
`

export const FormStyled = styled.form`


`

export const Bloque = styled.div`
    ${props => !props.activo && `display: none;`}
    display: flex;
    flex-direction: column;
    align-items: center;
    
`
export const CarouselStyled = styled(Carousel)`


`

export const Message = styled.div`
    ${props => props.severity === "error" && `
        background-color: #f44336;
        color: #fff;
    `}
    ${props => props.severity === "warn" && `
        background-color: #ff9800;
        color: #fff;
    `}
    width: max-content;
    padding: .4em 1em;
    margin: 1.5em auto;
    border-radius: .3em;
    
`