import React from 'react'
import { SHStyled } from './styled'

const SectionHeader = ({children, color="#333", background="#FFF", ...rest}) => {
    return (
        <SHStyled {...{color, background, ...rest}}>
            <h3>
                {children}
            </h3>
        </SHStyled>
    )
}

export default SectionHeader
